@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --color-bg-main: #fff;
  --color-text-main: #000;
  --color-bg-footer: #f7f7f7;
  --color-text-footer: #545454;
  --color-theme-svg-light: #ff6b08;
  --color-theme-svg-dark: #f7f7f7;
  --color-text-accent: #ff6b08;
  --color-reiting-bg: #ff6b01;
  --pagination-color: #ff6b08;
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--color-text-main);
  background-color: var(--color-bg-main);
  font-family: Roboto, sans-serif;
  position: relative;
}

main {
  position: relative;
}

.btn {
  cursor: pointer;
  border: none;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.container {
  min-width: 320px;
  width: 100vw;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 420px) {
  .container {
    width: 420px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  width: 100%;
  background-image: url("abstract-background-with-red-lines_1280.c59d864f.jpg");
  background-size: cover;
  padding-top: 15px;
  padding-bottom: 40px;
}

@media screen and (max-width: 767px) {
  .header {
    z-index: 100;
    position: fixed;
  }
}

@media screen and (min-width: 768px) {
  .header {
    background-image: url("abstract-background-with-red-lines_768.ecc07a54.jpg");
    padding-top: 15px;
    padding-bottom: 80px;
  }
}

@media screen and (min-width: 1280px) {
  .header {
    background-image: url("abstract-background-with-red-lines_320.91f63ae8.jpg");
  }
}

.header_lib {
  background-image: url("Rectangle 320.0ef92843.jpg");
  background-size: cover;
  padding-top: 10px;
  padding-bottom: 25px;
}

@media screen and (min-width: 768px) {
  .header_lib {
    background-image: url("Rectangle_768.8e64b2f9.jpg");
    padding-top: 15px;
    padding-bottom: 80px;
  }
}

@media screen and (min-width: 1280px) {
  .header_lib {
    background-image: url("Rectangle 1280 2x.a391ef16.jpg");
  }
}

.language_box {
  justify-content: right;
  margin-bottom: 8px;
  display: flex;
}

.language__item:not(:last-child) {
  margin-right: 8px;
}

.change__language {
  color: #fff;
  background: none;
  border: 0;
  outline: none;
  font-size: 12px;
  line-height: 12px;
}

.link__box {
  justify-content: space-between;
  display: flex;
  position: relative;
}

.logo__box {
  align-items: center;
  transition: scale .4s ease-in-out;
  display: flex;
}

.logo__box:hover, .logo__box:focus {
  scale: 1.1;
}

.navigation__link {
  text-transform: uppercase;
  color: #fff;
  text-shadow: 0 0 10px red, 0 0 20px red, 0 0 30px red, 0 0 40px red;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
}

.navigation__item {
  transition: scale .4s ease-in-out;
}

.navigation__item:hover, .navigation__item:focus {
  scale: 1.2;
}

.navigation__item:not(:last-child) {
  margin-right: 20px;
}

@media screen and (min-width: 768px) {
  .navigation__item:not(:last-child) {
    margin-right: 39px;
  }
}

.navigation__list {
  justify-content: right;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.form {
  text-align: center;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 767.98px) {
  .form {
    transition: width .4s ease-in-out;
    position: absolute;
    top: 20px;
  }
}

@media screen and (min-width: 768px) {
  .form {
    margin-top: 41px;
  }
}

.header:hover .form {
  display: block;
}

.header:hover .language_box {
  display: flex;
}

.input__form {
  width: 60%;
  color: #fff;
  background-color: rgba(0, 0, 0, .01);
  border-width: .5px;
  border-color: #fff;
  border-top-style: none;
  border-left-style: none;
  border-right-style: none;
  outline: none;
  transition: width .4s ease-in-out;
  position: relative;
}

@media screen and (min-width: 768px) {
  .input__form {
    width: 336px;
  }
}

@media screen and (min-width: 1280px) {
  .input__form {
    width: 394px;
  }
}

.image__logo {
  width: 24px;
  height: 24px;
  margin-right: 12px;
  display: flex;
}

.input__form::-ms-input-placeholder {
  color: #fff;
}

.input__form::placeholder {
  color: #fff;
}

.logo {
  color: #fff;
  text-shadow: 0 0 10px red, 0 0 20px red, 0 0 30px red, 0 0 40px red;
  font-size: 30px;
  transition: color .4s ease-in-out;
  display: none;
}

@media screen and (min-width: 768px) {
  .logo {
    display: block;
  }
}

.logo__box:hover .logo {
  color: tomato;
}

.submit {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background: url("search_2.082eef79.svg") center no-repeat;
  border: none;
  transition: scale .45s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  top: 5px;
  right: 25px;
}

.submit:hover, .submit:focus {
  transition: scale .45s cubic-bezier(.4, 0, .2, 1);
  scale: 1.5;
}

.current {
  -webkit-text-decoration: underline #ff001b;
  text-decoration: underline #ff001b;
  text-decoration-thickness: 3px;
}

.button__list {
  justify-content: center;
  margin: 40px auto 0;
  padding: 0;
  text-decoration: none;
  display: flex;
}

.button__item:not(:last-child) {
  margin-right: 16px;
}

.button_action {
  width: 148px;
  height: 42px;
  color: #fff;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, .01);
  border: 1px solid #fff;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  transition: scale .45s cubic-bezier(.4, 0, .2, 1);
}

.button_action:hover, .button_action:focus {
  transition: scale .45s cubic-bezier(.4, 0, .2, 1);
  scale: 1.1;
}

.active {
  background-color: #ff6b01;
  border: none;
}

.error__message {
  color: #ff001b;
  width: 140%;
  margin-top: 5px;
  font-size: 80%;
  line-height: 1.1;
  display: none;
  position: absolute;
  left: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .error__message {
    color: #ff001b;
    width: 100%;
    margin-top: 20px;
    font-size: 100%;
    line-height: 1.15;
  }
}

.switch {
  width: 36px;
  height: 19px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  border: 1px solid var(--color-text-accent);
  z-index: 2;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 13px;
  width: 13px;
  background-color: var(--color-text-accent);
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 3px;
}

input:checked + .slider:before {
  transform: translateX(16px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.switcher {
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: 42px;
  display: flex;
}

.switcher-theme-name {
  background: none;
  border: none;
  outline: none;
  font-size: 16px;
  line-height: 24px;
}

.dark {
  fill: var(--color-theme-svg-dark);
  justify-content: center;
  align-items: center;
  margin-left: 5px;
  display: flex;
}

.light {
  fill: var(--color-theme-svg-light);
  justify-content: center;
  align-items: center;
  margin-right: 5px;
  display: flex;
}

.title_box {
  justify-content: flex-end;
  display: flex;
}

.pagination ul {
  width: 100%;
  justify-content: center;
  padding-bottom: 60px;
  display: flex;
}

.pagination ul li {
  color: var(--color-text-main);
  text-align: center;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 12px;
  font-weight: 500;
  line-height: 40px;
  list-style: none;
  transition: all .3s;
}

.pagination ul li.numb {
  height: 40px;
  width: 40px;
  border-radius: 5px;
  margin: 0 3px;
  line-height: 40px;
  list-style: none;
}

.pagination ul li.dots {
  cursor: default;
  align-items: center;
  font-size: 12px;
}

.pagination ul li.btn-skip {
  width: 40px;
  height: 40px;
  background: var(--color-bg-footer);
  border-radius: 5px;
  justify-content: center;
  padding: 12px;
}

.pagination li.active, .pagination ul li.numb:hover, .pagination ul li:first-child:hover, .pagination ul li:last-child:hover {
  color: var(--color-bg-main);
  background: var(--pagination-color);
}

.prev {
  margin-right: 3px;
}

.next {
  margin-left: 3px;
}

@media screen and (max-width: 767px) {
  .dots, .first, .last {
    display: none;
  }
}

.svg-btn {
  fill: currentColor;
}

.span-svg {
  justify-content: center;
  display: flex;
}

* {
  margin: 0;
  padding: 0;
}

.content {
  min-height: calc(100vh - 80px);
}

.footer {
  background-color: var(--color-bg-footer);
  margin: 30px 0 0;
}

.footer__block {
  letter-spacing: .02em;
  color: var(--color-text-footer);
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer__block {
    flex-direction: row;
    justify-content: center;
    font-size: 16px;
    display: flex;
  }
}

.footer__content {
  margin-top: 29px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer__content {
    margin-bottom: 29px;
    display: flex;
  }
}

.footer__develop {
  margin-bottom: 29px;
  margin-right: 68px;
  padding: 5px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer__develop {
    margin-top: 29px;
  }
}

.btn {
  width: 55px;
  padding-left: 4px;
  display: flex;
}

.btn:after {
  content: "";
  width: 150px;
  height: 4px;
  background-color: #ff6b01;
  border-radius: 2px;
  margin-top: 20px;
  transition: transform .25s linear;
  display: block;
  position: absolute;
  transform: scale(0);
}

.btn:hover:after {
  transform: scale(1);
}

.btn:hover {
  font-weight: bold;
  transition: transform .25s linear;
}

.goit__stud {
  color: var(--color-text-footer);
  padding-left: 4px;
}

.footer__before-students {
  padding-right: 4px;
}

.animate__heart {
  width: 14px;
  height: 13px;
  margin: 0 5px;
  animation: beat 2s cubic-bezier(.4, 0, .2, 1) infinite;
}

@keyframes beat {
  0% {
    transform: scale(1);
  }

  14% {
    transform: scale(1.8);
  }

  28% {
    transform: scale(1.5);
  }

  42% {
    transform: scale(1.8);
  }

  70% {
    transform: scale(1.5);
  }
}

button {
  cursor: pointer;
  color: #ff6b01;
  background-color: var(--color-bg-footer);
  border: none;
  padding: 0;
}

.gallery__section {
  position: relative;
}

@media screen and (max-width: 767.98px) {
  .gallery__section {
    padding-top: 110px;
  }
}

.gallery, .gallery-lib {
  flex-wrap: wrap;
  display: flex;
}

.gallery__item {
  transition: var(--animations-link-shadow);
  transition: transform .45s cubic-bezier(.4, 0, .2, 1);
}

.gallery__item:hover, .gallery__item:focus {
  transform: scale(1.015);
  box-shadow: 10px 10px 10px rgba(0, 0, 0, .12), 40px 40px 40px rgba(0, 0, 0, .06), 10px 40px 60px rgba(0, 0, 0, .16);
}

@media screen and (max-width: 767.98px) {
  .gallery__item {
    width: 280px;
    margin-left: auto;
    margin-right: auto;
  }

  .gallery__item:not(:last-child) {
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279.98px) {
  .gallery__item {
    flex-basis: calc(50% - 16px);
    margin-top: 22px;
  }

  .gallery__item:nth-child(2n-1) {
    margin-right: 32px;
  }

  .gallery__item:nth-child(-n+2) {
    margin-top: 0;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__item {
    flex-basis: calc(33.3333% - 10.6667px);
    margin-top: 22px;
    margin-right: 16px;
  }

  .gallery__item:nth-child(3n) {
    margin-right: 0;
  }

  .gallery__item:nth-child(-n+3) {
    margin-top: 0;
  }
}

.gallery__card {
  color: currentColor;
  text-decoration: none;
}

.gallery__subtitle {
  letter-spacing: .06em;
  text-transform: uppercase;
  color: var(--color-text-main);
  margin-top: 10px;
  margin-left: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
  .gallery__subtitle {
    margin-top: 8px;
    line-height: 1.33;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__subtitle {
    margin-top: 12px;
    font-size: 20px;
    line-height: 1.2;
  }
}

.gallery__genres {
  letter-spacing: .06em;
  color: var(--color-text-accent);
  margin-left: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
  .gallery__genres {
    line-height: 1.33;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__genres {
    font-size: 20px;
    line-height: 1.2;
  }
}

.gallery__year {
  letter-spacing: .06em;
  color: var(--color-text-accent);
  margin-left: 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  position: relative;
}

@media screen and (min-width: 768px) {
  .gallery__year {
    margin-left: 12px;
    line-height: 1.33;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__year {
    margin-left: 18px;
    font-size: 20px;
    line-height: 1.2;
  }
}

.gallery__year:before {
  content: "|";
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-200%);
}

.gallery__vote-average {
  text-transform: uppercase;
  color: var(--color-bg-main);
  background-color: var(--color-reiting-bg);
  width: 36px;
  height: 16px;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-left: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .gallery__vote-average {
    width: 40px;
    height: 20px;
  }
}

.gallery__foto {
  width: 280px;
  height: 402px;
  object-fit: cover;
  display: block;
}

@media screen and (min-width: 768px) {
  .gallery__foto {
    width: 336px;
    height: 455px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__foto {
    width: 395px;
    height: 574px;
  }
}

.gallery__info {
  padding-bottom: 10px;
  display: flex;
}

.link__arrow {
  background-color: rgba(0, 0, 0, 0);
  padding: 10px;
  animation: bounce-top 6s infinite alternate-reverse;
  position: fixed;
  bottom: 75px;
  right: 20px;
}

@media screen and (max-width: 767.98px) {
  .link__arrow {
    display: none;
  }
}

@media screen and (max-width: 1023px) {
  .link__arrow {
    bottom: 75px;
    right: -10px;
  }
}

.link__arrow:focus, .link__arrow:hover {
  transform: scale(1.2);
}

@keyframes bounce-top {
  0% {
    opacity: 1;
    animation-timing-function: ease-in;
    transform: translateY(-45px);
  }

  24% {
    opacity: 1;
  }

  40% {
    animation-timing-function: ease-in;
    transform: translateY(-24px);
  }

  65% {
    animation-timing-function: ease-in;
    transform: translateY(-12px);
  }

  82% {
    animation-timing-function: ease-in;
    transform: translateY(-6px);
  }

  93% {
    animation-timing-function: ease-in;
    transform: translateY(-4px);
  }

  25%, 55%, 75%, 87% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  100% {
    opacity: 1;
    animation-timing-function: ease-out;
    transform: translateY(0);
  }
}

.svg__arrow {
  fill: #ff6b01;
  width: 75px;
  height: 75px;
}

@media screen and (max-width: 1023px) {
  .svg__arrow {
    width: 35px;
    height: 35px;
  }
}

.mask {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(0, 0, 0, .4);
  justify-content: center;
  align-items: center;
  transition: all .6s;
  display: flex;
  position: fixed;
  top: 0;
}

.hide {
  opacity: 0;
  pointer-events: none;
}

.loader {
  border-right: 1px solid var(--pagination-color);
  border-radius: 100%;
  animation: loader .8s linear infinite;
}

.loader:before, .loader:after {
  content: "";
  width: 120px;
  height: 120px;
  border-left: 5px solid var(--pagination-color);
  border-radius: 100%;
  animation: loader .4s linear infinite reverse;
  display: block;
  position: absolute;
